import React from "react";
import PropTypes from "prop-types";
import {connect} from "react-redux";
import OraTooltip from "eshop/modules/core/components/ui/Tooltip";
import OnlineUtils from "eshop/utils/OnlineUtils";
import {getPriceIsNet} from "eshop/modules/cart/selectors";
import {getContractRole} from 'eshop/modules/configurator/selectors/offers';

class OraPriceSummaryComponent extends React.Component {

    constructor(props) {
        super(props);
    }


    componentShouldRender() {
        if (this.props.deviceTotalPrice != null && this.props.basePrice != null && this.props.basePrice.combinedTariffAndDevicePayments != null) {
            return true;
        } else {
            return false;
        }
    }

    render() {
        let deviceTotalPrice = this.props.priceIsNet ? this.props.deviceTotalPriceNet : this.props.deviceTotalPriceGross;

        return (
            <div className="u-padding-top u-clear-both">
                <div className="opl-console">
                    <p><span
                        className="u-font-bold u-font-small">{OnlineUtils.formatPrice(deviceTotalPrice)}&nbsp;zł</span> łączna
                        cena za telefon</p>
                    {this.props.shouldShowStock &&
                    <p><span className="u-font-bold u-font-small">{this.props.stockLevel} szt.</span> dostępne</p>}
                    {this.props.shouldShowSum &&
                    <p>suma opłat (abonament + telefon) <OraTooltip className="u-inline" maxWidth={450}
                                                                    direction="bottom">{this.tooltipContent()}</OraTooltip>
                    </p>}
                </div>
            </div>
        );
    }

    isSimfreeInstallment() {
        return this.props.offerType === 'SIMFREE_INST';
    }

    tieredPriceIsEmpty(tieredPrice) {
        return tieredPrice.monthFrom === 0 && tieredPrice.monthTo === 0 && tieredPrice.priceNet === 0;
    }

    tooltipContent() {
        if (!this.getCombinedPrice()) {
            return null
        }
        return (
            <div>
                {this._renderOneTimeCharges()}
                <hr className="u-padding-l"/>
                {!this.isSimfreeInstallment() && this._renderOnFirstBill()}
                {this.getCombinedPrice() ? this.getCombinedPrice().filter(tieredPrice => !this.tieredPriceIsEmpty(tieredPrice)).map((tieredPrice) => this._renderCombinedTerminalAndTariffPayment(tieredPrice)) :
                    ""}
            </div>
        )

    }

    combinePayments(tariffForPeriod,terminalForPeriod,monthFrom,monthTo){
        let combined={};
        combined.priceNet=(!!tariffForPeriod?tariffForPeriod.originalNetPrice:0)+(!!terminalForPeriod?terminalForPeriod.priceNet:0.0);
        combined.priceGross=(!!tariffForPeriod?tariffForPeriod.originalGrossPrice:0)+(!!terminalForPeriod?terminalForPeriod.priceGross:0.0);
        combined.monthFrom=monthFrom;
        combined.monthTo=monthTo;
        return combined;
    }

    getCombinedPrice() {
        let contractRolePayment=this.props.proposition&&OnlineUtils.getPaymentsForRole(this.props.proposition.payments, this.props.contractRole);
        let tariffPayments = (contractRolePayment && contractRolePayment.totalPayments) ? contractRolePayment.totalPayments.monthlyPayments : [];
        let terminalPayments = this.props.basePrice&&this.props.basePrice.devicePayments;
        let monthsFrom = [];
        let monthsTo = [];

        if (!!tariffPayments && tariffPayments[0]) {
            OnlineUtils.addUniqueIntoArray(tariffPayments[tariffPayments.length - 1].monthTo, monthsTo);
            OnlineUtils.addUniqueIntoArray(tariffPayments[tariffPayments.length - 1].monthTo + 1, monthsFrom);
            for (let i = 0; i < tariffPayments.length; i++) {
                OnlineUtils.addUniqueIntoArray(tariffPayments[i].monthFrom, monthsFrom);
                OnlineUtils.addUniqueIntoArray(tariffPayments[i].monthTo, monthsTo);
            }
        }
        if (!!terminalPayments && terminalPayments[0]) {
            OnlineUtils.addUniqueIntoArray(terminalPayments[terminalPayments.length - 1].monthTo, monthsTo);
            OnlineUtils.addUniqueIntoArray(terminalPayments[terminalPayments.length - 1].monthTo + 1, monthsFrom);
            for (let i = 0; i < terminalPayments.length; i++) {
                OnlineUtils.addUniqueIntoArray(terminalPayments[i].monthFrom, monthsFrom);
                OnlineUtils.addUniqueIntoArray(terminalPayments[i].monthTo, monthsTo);
            }

        }




        monthsFrom = monthsFrom.sort((a, b) => {
            return a - b
        });
        monthsTo = monthsTo.sort((a, b) => {
            return a - b
        });

        let payments = [];
        for (let i = 0; i < monthsFrom.length - 1; i++) {
            let tariffForPeriod = this.getTariffPriceForPeriod(monthsFrom[i], monthsTo[i]);
            let terminalForPeriod = this.getTerminalPriceForPeriod(monthsFrom[i], monthsTo[i]);
            if (!!tariffForPeriod || !!terminalForPeriod)
                payments.push(this.combinePayments(tariffForPeriod, terminalForPeriod, monthsFrom[i], monthsTo[i]));
        }
        return payments;
    }

    _renderOneTimeCharges() {
        let oneTimePayment;
        if(!!this.props.basePrice){
            oneTimePayment = this.props.priceIsNet ? this.props.basePrice.oneTimePaymentNet : this.props.basePrice.oneTimePaymentGross;
        }
        return (<div>
            <div className="l-row u-padding-top">
                <div className={"u-font-bold " + FULL_LINE}>Na start</div>
            </div>
            <div className="l-row">
                <div className={LEFT_COLUMN}>opłaty jednorazowe</div>
                <div className={"u-font-bold" + RIGHT_COLUMN}>{OnlineUtils.formatPrice(oneTimePayment)}&nbsp;zł</div>
            </div>
        </div>)

    }


    _renderOnFirstBill() {
        let tieredPrice = this.getCombinedPrice()[0];
        let payNowPayment;
        let totalPrice;

        if(!!this.props.basePrice &&  !!this.props.basePrice.payNowPayment)
            payNowPayment = this.props.priceIsNet ? this.props.basePrice.payNowPayment.priceNet : this.props.basePrice.payNowPayment.priceGross;

        if(!!tieredPrice)
            totalPrice = this.props.priceIsNet ? (tieredPrice.priceNet) : (tieredPrice.priceGross);

        return (<div>

                <div className="l-row">
                    <div className={"u-font-bold " + FULL_LINE}>W pierwszym miesiącu</div>
                </div>
                {<div className="l-row">
                    <div className={LEFT_COLUMN}>opłaty jednorazowe</div>
                    <div className={"u-font-bold" + RIGHT_COLUMN}>{OnlineUtils.formatPrice(payNowPayment)}</div>
                </div>}
                {(!!this.props.basePrice&& this.props.basePrice.combinedTariffAndDevicePayments) != null ?
                    <div className="l-row">
                        <div className={LEFT_COLUMN}>opłaty miesięczne</div>
                        <div
                            className={"u-font-bold" + RIGHT_COLUMN}>{OnlineUtils.formatPrice(totalPrice)}&nbsp;zł
                        </div>
                    </div> : <div/>
                }

            </div>

        )
    }
    getTerminalPriceForPeriod(monthFrom,monthTo){
        return this.props.basePrice&&this.props.basePrice.devicePayments.find(device=>device.monthFrom<=monthFrom && device.monthTo>=monthTo);
    }

    getTariffPriceForPeriod(monthFrom,monthTo){
        let payments = this.props.proposition && OnlineUtils.getPaymentsForRole(this.props.proposition.payments, this.props.contractRole);
        return payments && payments.totalPayments.monthlyPayments.find(tariff=>tariff.monthFrom<=monthFrom && tariff.monthTo>=monthTo);
    }

    _renderCombinedTerminalAndTariffPayment(tieredPrice) {
        if(!tieredPrice)
                return null;

        let totalPrice = this.props.priceIsNet ? (tieredPrice.priceNet) : (tieredPrice.priceGross);
        return (<div id={tieredPrice.monthFrom}>
                <div className="l-row u-padding-top">
                    <div
                        className={"u-font-bold" + FULL_LINE}>od {tieredPrice.monthFrom == 1 ? 2 : tieredPrice.monthFrom} do {tieredPrice.monthTo} miesiąca
                    </div>
                </div>
                <div className="l-row">
                    <div className={LEFT_COLUMN}>opłaty miesięczne</div>
                    <div className={"u-font-bold" + RIGHT_COLUMN}>{OnlineUtils.formatPrice(totalPrice)}&nbsp;zł</div>
                </div>
            </div>
        );

    }
}


const FULL_LINE = " l-col l-col-12 u-font-small "
const LEFT_COLUMN = " l-col l-col-8 u-font-small "
const RIGHT_COLUMN = " l-col l-col-4 u-text-right g-brand1-c u-font-small "

OraPriceSummaryComponent.defaultProps = {
    stockLevel: "",
    tooltipContent: "",
    deviceTotalPrice: ""
};


OraTooltip.propTypes = {
    stockLevel: PropTypes.string
};

const mapStateToProps = state => ({
    priceIsNet: getPriceIsNet(state),
    contractRole: getContractRole(state),
})

export default connect(mapStateToProps, null)(OraPriceSummaryComponent)
